/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* Reset DEFAULT CDZ */
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

textarea {
  resize: none;
}

button::-moz-focus-inner,
input[type=reset]::-moz-focus-inner,
input[type=button]::-moz-focus-inner,
input[type=submit]::-moz-focus-inner {
  border: none;
  padding: 0;
}

input[type=search] {
  -webkit-appearance: none;
}

button,
input[type=button],
input[type=reset],
input[type=submit] {
  cursor: pointer;
  -webkit-appearance: none;
}

a:focus,
button:focus,
input:focus,
textarea:focus,
select:focus {
  outline: none;
}

input,
textarea,
select,
button {
  font: inherit;
  border-radius: 0;
}

/* Remove controls from Firefox */
input[type=number] {
  -moz-appearance: textfield;
}

input[type=number]:hover,
input[type=number]:focus {
  -moz-appearance: textfield;
}

/* Remove controls from Safari and Chrome */
input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type=text],
input[type=email],
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

/* / Reset DEFAULT CDZ */
@font-face {
  font-family: "Playfair Display";
  src: url("../fonts/subset-PlayfairDisplay-Black.eot");
  src: url("../fonts/subset-PlayfairDisplay-Black.eot?#iefix") format("embedded-opentype"), url("../fonts/subset-PlayfairDisplay-Black.woff2") format("woff2"), url("../fonts/subset-PlayfairDisplay-Black.woff") format("woff"), url("../fonts/subset-PlayfairDisplay-Black.ttf") format("truetype"), url("../fonts/subset-PlayfairDisplay-Black.svg#PlayfairDisplay-Black") format("svg");
  font-weight: 900;
  font-style: normal;
  font-display: swap;
}
body {
  font-family: "Playfair Display", sans-serif;
  font-size: 16px;
}

#app {
  position: relative;
  overflow: hidden;
}

.container {
  padding: 0 1.5rem;
  margin: 0 auto;
}

.wrap-background-image {
  position: relative;
}
.wrap-background-image .background-image {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.wrap-background-image .background-image img {
  display: none;
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border-width: 0;
}

.header {
  background: #56CC9D;
  width: 100%;
  position: fixed;
  z-index: 1;
}
.header .wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header .wrapper .logo {
  width: 12.5rem;
  padding: 0.625rem 0.625rem 0.875rem 1.25rem;
}
.header .wrapper .logo a {
  text-decoration: none;
  text-transform: uppercase;
  letter-spacing: 0.3125rem;
  font-size: 2rem;
  color: #fff;
}
.header .wrapper .navigation .menu {
  display: flex;
  align-items: center;
}
.header .wrapper .navigation .menu li {
  padding: 0.9375rem 1.25rem;
}
.header .wrapper .navigation .menu li a {
  text-decoration: none;
  text-transform: uppercase;
  font-family: monospace;
  font-size: 1.25rem;
  letter-spacing: 0.125rem;
  color: #fff;
  position: relative;
}
.header .wrapper .navigation .menu li a:hover::after {
  width: 100%;
}
.header .wrapper .navigation .menu li a::after {
  content: "";
  display: block;
  height: 2px;
  width: 0px;
  background: #fff;
  margin-top: 4px;
  transition: 0.3s;
  position: absolute;
}

.title > h2 {
  font-size: 2rem;
}
.title > h3 {
  font-size: 2rem;
}
.title > h4 {
  font-size: 1.375rem;
}

.banner-introduction {
  background: #000;
}
.banner-introduction .banner .banner-image {
  display: flex;
  justify-content: center;
  position: relative;
}
.banner-introduction .banner .banner-image .title {
  position: absolute;
  bottom: 12%;
  left: 50%;
  transform: translateX(-50%);
}
.banner-introduction .banner .banner-image .title h2 {
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 0.125rem;
  line-height: 0;
}

.spirit-animals {
  padding: 3.75rem 0;
}
.spirit-animals .wrapper .spirit-animals-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.spirit-animals .wrapper .spirit-animals-header .title {
  margin: 1.25rem 0;
}
.spirit-animals .wrapper .spirit-animals-header .title h2 {
  color: #3B3B3B;
}
.spirit-animals .wrapper .spirit-animals-header .buttons {
  margin: 1.25rem 0 2.5rem 0;
}
.spirit-animals .wrapper .spirit-animals-header .buttons ul {
  display: flex;
  align-items: center;
  justify-content: center;
}
.spirit-animals .wrapper .spirit-animals-header .buttons ul li .button {
  display: inline-block;
  color: #56CC9D;
  border: 0.25rem solid;
  padding: 0.625rem 0;
  width: 12.5rem;
  margin: 0.625rem;
  text-decoration: none;
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 0.125rem;
}
.spirit-animals .wrapper .spirit-animals-header .buttons ul li .button.active {
  background: #56CC9D;
  padding: 0.625rem 0;
  color: #fff;
  border: 0.25rem solid #56CC9D;
}
.spirit-animals .wrapper .spirit-animals-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2.5rem;
  justify-content: center;
}
.spirit-animals .wrapper .spirit-animals-content .img-animal {
  justify-self: end;
  position: relative;
}
.spirit-animals .wrapper .spirit-animals-content .img-animal .title {
  bottom: 10%;
  right: -3%;
  position: absolute;
  min-width: 12.5rem;
  padding: 0.3125rem 1.25rem 0.625rem 1.25rem;
  background: #FF4338;
}
.spirit-animals .wrapper .spirit-animals-content .img-animal .title h2 {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  letter-spacing: 0.3125rem;
}
.spirit-animals .wrapper .spirit-animals-content .spirit-animals-description .spirit-animals-row {
  max-width: 28.125rem;
}
.spirit-animals .wrapper .spirit-animals-content .spirit-animals-description .spirit-animals-row .title {
  margin: 1.25rem 0;
}
.spirit-animals .wrapper .spirit-animals-content .spirit-animals-description .spirit-animals-row .title h4 {
  color: #56CC9D;
}
.spirit-animals .wrapper .spirit-animals-content .spirit-animals-description .spirit-animals-row .paragraph {
  margin-bottom: 2.5rem;
}
.spirit-animals .wrapper .spirit-animals-content .spirit-animals-description .spirit-animals-row .paragraph p {
  color: rgba(0, 0, 0, 0.7);
}

.paragraph {
  font-size: 1.25rem;
  line-height: 1.4;
  font-family: monospace;
}